import * as stream from 'stream';
import { Agent, Field, InputType, Observer, SkillRequest } from "./decorators";
import { makeRequest } from './outbound';
import { Void } from './types';

@InputType()
class PlayReq {
  @Field({optional: true})
  duration: number;

  @Field()
  speed: number;

  @Field()
  start_time: string;
}

@Agent({ abstract: true })
export abstract class SimAgent {
  simName: string;
  dataSource: stream.Readable;
  firstEventTime: Date;
  iterator: AsyncIterator<object>;
  localBaseTime: number;
  simBaseTime: number;
  simStopTime?: number;
  speed: number;
  done: boolean;
  paused: boolean;
  nextRecord: object;
  lastRecordTime: Date;
  lastPrint: number;
  recordCount: number;
  cancelRunLoop: Function;
  canceled: Promise<void>;

  constructor() {
    this.dataSource = null;
    this.nextRecord = null;
  }

  // Called by framework once server is up
  started() {
    this.simInitSource().then(() => {
      this.simName = this.constructor.name;
      this.firstEventTime = this.getNextTime(this.nextRecord);
      setTimeout(() => {
        const start = this.firstEventTime ? isoTime(this.firstEventTime) : '';
        this.registerSim(this.simName, start).catch(console.error);
      }, 3000)
    });    
  }

  @Observer({ subject: 'sim_control', action: 'roll_call'})
  rollCall() {
    const start = this.firstEventTime ? isoTime(this.firstEventTime) : '';
    this.registerSim(this.simName, start).catch(console.error);
  }

  @Observer({ subject: 'sim_control', action: 'play'})
  simPlay({duration, speed, start_time}: PlayReq) {
    console.log('PLAY', start_time, duration, speed);
    const simDate = new Date(start_time);
    this.simBaseTime = simDate.getTime();
    this.simStopTime = duration ? this.simBaseTime + duration * 1000 : null;
    this.localBaseTime = new Date().getTime();
    this.speed = speed;
    this.startSim();
  }

  @Observer({ subject: 'sim_control', action: 'stop'})
  simStop() {
    console.log('USER STOP');
    this.paused = true;
    this.cancelRunLoop();
  }
  
  @Observer({ subject: 'sim_control', action: 'reset'})
  simReset() {
    if (this.dataSource) {
      this.dataSource[Symbol.asyncDispose]().catch(err => console.error(err));
      this.dataSource = null;
    }
  }

  @SkillRequest({ subject: 'sim_control', action: 'register_sim_agent', returns: Void })
  registerSim(name: string, start_time: string) {
    return makeRequest<void>();
  }

  @SkillRequest({ subject: 'sim_control', action: 'sim_agent_stopped', returns: Void })
  signalStopped(name: string, stop_time: string) {
    return makeRequest<void>();
  }

  @SkillRequest({ subject: 'sim_control', action: 'sim_agent_finished', returns: Void })
  signalFinished(name: string, end_time: string) {
    return makeRequest<void>();
  }

  async startSim() {
    if (this.lastRecordTime && this.lastRecordTime.getTime() > this.simBaseTime) {
      console.log('RESETTING SOURCE');
      this.dataSource.destroy();
      this.dataSource = null;
      await this.simInitSource();
    }

    // Forward to start time
    var nxt = this.getNextTime(this.nextRecord);
    var skipped = 0;
    while (this.nextRecord && nxt) {
      if (nxt.getTime() < this.simBaseTime) {
        await this.advanceRecord();
        nxt = this.getNextTime(this.nextRecord);
        skipped += 1;
      } else {
        break;
      }
    }
    if (skipped) {
      console.log('SKIPPED', skipped, 'records');
    }

    const sim = this;
    this.canceled = new Promise((accept, reject) => {
      sim.paused = false;
      sim.cancelRunLoop = accept;
      sim.lastPrint = 0;
      sim.simRunLoop()
        .then(() => {
          if (this.done)
            this.signalFinished(this.simName, isoTime(this.lastRecordTime));
          else
            this.signalStopped(this.simName, isoTime(this.lastRecordTime));
        })
        .catch(err => console.error(err));
    });    
  }

  async simInitSource() {
    if (!this.dataSource) {
      console.log('Creating data source');
      this.dataSource = this.createDataSource();
      this.iterator = this.dataSource[Symbol.asyncIterator]();
      this.recordCount = 0;
      await this.advanceRecord();
      console.log('First record', this.nextRecord);
      this.paused = false;
      this.done = false;
    }
  }

  async simRunLoop() {
    var nxt = this.getNextTime(this.nextRecord);
    if (!nxt)
      this.done = true;
  
    while (!this.done) {
      await this.waitUntil(nxt);

      const now = new Date();
      if ((now.getTime() - this.lastPrint) >= 1000) {
        console.log(`${now.toISOString()}: @ ${nxt.toISOString()}, ${this.recordCount} records`);
        this.lastPrint = now.getTime();
      }

      await this.step(this.nextRecord);
      this.lastRecordTime = nxt;

      await this.advanceRecord();
      nxt = this.getNextTime(this.nextRecord);
      if (nxt) {
        if (this.simStopTime && nxt.getTime() >= this.simStopTime) {
          console.log('DURATION STOP');
          this.paused = true;
          break;
        }
        // Returns early if this.canceled is resolved, in which case should must be true
        if (this.paused)
          break;
      } else
        this.done = true;
    }
    if (this.done)
      console.log('Simulation done');
    else
      console.log('Simulation paused')
  }

  async waitUntil(dt: Date) {
    const realElapsedTime = (new Date().getTime() - this.localBaseTime);
    const target = Math.max(dt.getTime() - this.simBaseTime, 0) / this.speed;
    //console.log('waitUntil', realElapsedTime, target);
    if (target > realElapsedTime) {
      await waitFor(target - realElapsedTime);
    }
  }

  // for for ms milliseconds of simulation time
  simWaitFor(ms: number) {
    const real_ms = Math.floor(ms / this.speed);
    if (real_ms == 0)
      return Promise.resolve();
    return new Promise((accept, reject) => {
      setTimeout(accept, real_ms);
    });
  }

  async advanceRecord() {
    const value = await this.iterator.next();
    this.nextRecord = !value.done ? value.value : null;
    this.recordCount += 1;
  }

  abstract createDataSource(): stream.Readable;
  abstract getNextTime(record?: object): Date|undefined;
  abstract step(record: object);
}

function waitFor(ms: number) {
  return new Promise((accept, reject) => {
    setTimeout(accept, ms);
  });
}

function isoTime(d: Date) {
  return d.toISOString().substring(0,19)
}