import { GraphQLSchema, GraphQLObjectType, GraphQLString, GraphQLFloat, GraphQLBoolean, GraphQLNamedType, GraphQLInputObjectType, GraphQLFieldConfig, GraphQLNamedInputType, GraphQLNamedOutputType, GraphQLNonNull, GraphQLInputType, GraphQLOutputType, GraphQLInt, GraphQLList } from 'graphql';
import { AnswerDefinition, MetaField, MetaFunction, MetaParameter, ObserverDefinition, QuestionDefinition, SkillRequestDefinition, SkillDefinition, ValueType, getMetaStorage, isScalarType, isObjectParameter, isVoidType, isArrayType, isObjectType } from './metadata';
import { Conversation, getConversation, getOrCreateConversation } from './conversation';
import { handleSkillResponse } from './outbound';

var agentInstance: object = null;

export function generateSchema(inst: object) {
  const meta = getMetaStorage();
  const skills = meta.getSkills();
  const questions = meta.getQuestions();
  const answers = meta.getAnswers();
  const responses = meta.getSkillRequests();
  const observers = meta.getObservers();

  agentInstance = inst;

  var types = [];
  const queries = {
    _ping: {
      type: GraphQLBoolean,
      resolver: () => true
    }
  };

  var mutations = skills.map(skill => [skill.fun.name, generateSkillMutation(skill, types)]);
  mutations = mutations.concat(questions.map(q => [q.fun.name, generateQuestionMutation(q, types)]));
  mutations = mutations.concat(answers.map(ans => [ans.fun.name, generateAnswerMutation(ans, types)]));
  mutations = mutations.concat(responses.map(resp => [resp.fun.name, generateSkillResponseMutation(resp, types)]));
  mutations = mutations.concat(observers.map(obs => [obs.fun.name, generateObserverMutation(obs, types)]));

  const schema = new GraphQLSchema({
    query: new GraphQLObjectType({
      name: 'Query',
      fields: queries
    }),
    mutation: new GraphQLObjectType({
      name: 'Mutation',
      fields: Object.fromEntries(mutations),
    }),
    types
  });
  return schema;
}

interface GqlRoot {
}

interface GqlContext {
}

// parent, args, contextValue, info
function generateSkillMutation(skill: SkillDefinition, namedTypes: GraphQLNamedType[]): GraphQLFieldConfig<GqlRoot,GqlContext> {
  const agent = getMetaStorage().getAgent(skill.cls);
  const answers = getMetaStorage().getAnswers().filter(ans => ans.cls == skill.cls && ans.options.action == skill.options.action);
  var args = Object.fromEntries(parametersSchema(skill.fun.parameters, namedTypes).map(arg => [arg.name, arg]));
  if (skill.fun.conversation)
    args['_cid'] = {
      name: '_cid',
      type: new GraphQLNonNull(GraphQLString)
    };

  const type = (skill.fun.returnType && !skill.options.background) ? toOutputType(skill.fun.returnType, namedTypes) : GraphQLBoolean;
  return {
    args,
    type,
    resolve: (parent, args, contextValue, info) => {
      console.log('Handle', skill.cls, skill.fun.name);
      const conv = args._cid ? getOrCreateConversation(args._cid, agent, skill, answers) : null;
      if ('_cid' in args && skill.fun.conversation)
        args[skill.fun.conversation] = conv;
      // agentInstance will be null for sessions agent and skill will be a static member function
      const result = skill.impl.apply(agentInstance, buildArgs(skill.fun, args, conv));
      if (skill.options.background) {
        if (typeof result === 'object' && 'constructor' in result) {
          if (result.constructor.name === skill.cls)
            conv.instance = result;
        }
        return true;
      }
      return (result !== undefined) ? result : true;
    }
  };
}

function generateQuestionMutation(question: QuestionDefinition, namedTypes: GraphQLNamedType[]): GraphQLFieldConfig<GqlRoot,GqlContext> {
  var args = Object.fromEntries(parametersSchema(question.fun.parameters, namedTypes).map(arg => [arg.name, arg]));
  for (const extra of ['_cid', '_mid']) {
    args[extra] = {
      name: extra,
      type: new GraphQLNonNull(GraphQLString)
    };  
  }

  const type = (question.fun.returnType && !question.options.background) ? toOutputType(question.fun.returnType, namedTypes) : GraphQLBoolean;
  return {
    args,
    type,
    resolve: (parent, args, contextValue, info) => {
      console.log('Handle', question.cls, question.fun.name);
      const conv = getConversation(question.cls, args._cid);
      if (!conv)
        throw Error(`Missing conversation ${args._cid}!`);
      if (question.fun.conversation)
        args[question.fun.conversation] = conv;
      const result = question.impl.apply(conv.instance, buildArgs(question.fun, args, conv));
      return (result !== undefined) ? result : true;
    }
  };  
}

function generateAnswerMutation(answer: AnswerDefinition, namedTypes: GraphQLNamedType[]): GraphQLFieldConfig<GqlRoot,GqlContext> {
  var args = Object.fromEntries(parametersSchema(answer.fun.parameters, namedTypes).map(arg => [arg.name, arg]));
  args['_cid'] = {
    name: '_cid',
    type: new GraphQLNonNull(GraphQLString)
  };

  return {
    args,
    type: GraphQLBoolean,
    resolve: (parent, args, contextValue, info) => {
      console.log('Handle', answer.cls, answer.fun.name);
      const conv = getConversation(answer.cls, args._cid);
      if (!conv)
        throw Error(`Missing conversation ${args._cid}!`);
      if (answer.fun.conversation)
        args[answer.fun.conversation] = conv;
      answer.impl.apply(conv.instance, buildArgs(answer.fun, args, conv));
      return true;
    }
  };  
}

function generateObserverMutation(observer: ObserverDefinition, namedTypes: GraphQLNamedType[]): GraphQLFieldConfig<GqlRoot,GqlContext> {
  var args = Object.fromEntries(parametersSchema(observer.fun.parameters, namedTypes).map(arg => [arg.name, arg]));
  for (const extra of ['_cid']) {
    args[extra] = {
      name: extra,
      type: new GraphQLNonNull(GraphQLString)
    };  
  }

  return {
    args,
    type: GraphQLBoolean,
    resolve: (parent, args, contextValue, info) => {
      console.log('Handle', observer.cls, observer.fun.name);
      observer.impl.apply(agentInstance, buildArgs(observer.fun, args, null));
      return true;
    }
  };  
}

function generateSkillResponseMutation(skill: SkillRequestDefinition, namedTypes: GraphQLNamedType[]): GraphQLFieldConfig<GqlRoot,GqlContext> {
  var args = Object.fromEntries(returnSchema(skill.options.returns, namedTypes).map(arg => [arg.name, arg]));
  args['_uid'] = {
    name: '_uid',
    type: new GraphQLNonNull(GraphQLString)
  };

  return {
    args,
    type: GraphQLBoolean,
    resolve: (parent, args, contextValue, info) => {
      console.log('Handle', skill.cls, skill.fun.name);
      handleSkillResponse(args['_uid'], args);
      return true;
    }
  };  
}

function returnSchema(returns: ValueType, namedTypes: GraphQLNamedType[]) {
  if (isObjectType(returns) && !Array.isArray(returns)) {
    // Mock a destructured function input to reuse helper expecting a parameter list
    return parametersSchema([{
      name: '<anonymous>',
      destructured: true,
      type: returns
    }], namedTypes);
  } else if (isVoidType(returns)) {
    return [];
  } else {
    return [{
      name: 'result',
      type: toInputType(returns, namedTypes)
    }];
  }
}

function parametersSchema(params: MetaParameter[], namedTypes: GraphQLNamedType[]) {
  if (isObjectParameter(params)) {
    const type = params[0].type;
    const cls = getMetaStorage().getClass(type.name);
    return Object.keys(cls.fields)
      .map(field => {
        var type = toInputType(cls.fields[field].type, namedTypes);
        if (isRequred(cls.fields[field]))
          type = new GraphQLNonNull(type);
        return {
          name: field,
          type
        }
      });
  } else {
    return params.map(param => ({
      name: param.name,
      type: toInputType(param.type, namedTypes)
    }));  
  }
}

function toInputType(jsType: ValueType, namedTypes: GraphQLNamedType[]): GraphQLInputType {
  if (Array.isArray(jsType)) {
    return new GraphQLList(toInputType(jsType[0], namedTypes));
  } else {
    switch (jsType.name) {
      case 'Number':
        return GraphQLFloat;
  
      case 'Int':
        return GraphQLInt;
  
      case 'String':
        return GraphQLString;
  
      case 'Boolean':
        return GraphQLBoolean;
  
      default:
        const cls = getMetaStorage().getClass(jsType.name);
        var type  = namedTypes.find(t => t.name === cls.name) as GraphQLNamedInputType;
        if (!type) {
          const fields = Object.fromEntries(Object.keys(cls.fields).map(field => {
            var type = toInputType(cls.fields[field].type, namedTypes);
            if (isRequred(cls.fields[field]))
              type = new GraphQLNonNull(type);
            return [field, { type }];
          }));
          type = new GraphQLInputObjectType({
            name: cls.name,
            fields
          });
          namedTypes.push(type);
        }
        return type;
    }  
  }
}

function toOutputType(jsType: ValueType, namedTypes: GraphQLNamedType[]): GraphQLOutputType {
  if (Array.isArray(jsType)) {
    return new GraphQLList(toOutputType(jsType[0], namedTypes));
  } else {
    switch (jsType.name) {
      case 'Number':
        return GraphQLFloat;
  
      case 'Int':
        return GraphQLInt;
    
      case 'String':
        return GraphQLString;
  
      case 'Boolean':
        return GraphQLBoolean;
  
      default:
        const cls = getMetaStorage().getClass(jsType.name);
        var type  = namedTypes.find(t => t.name === cls.name) as GraphQLNamedOutputType;
        if (!type) {
          const fields = Object.fromEntries(Object.keys(cls.fields).map(field => {
            var type = toOutputType(cls.fields[field].type, namedTypes);
            if (isRequred(cls.fields[field]))
              type = new GraphQLNonNull(type);
            return [field, { type }];
          }));
          type = new GraphQLObjectType({
            name: cls.name,
            fields
          });
          namedTypes.push(type);
        }
        return type;
    }  
  }
}

function buildArgs(fun: MetaFunction, args: object, conv: Conversation) {
  var ary = [];
  if (isObjectParameter(fun.parameters)) {
    ary.push(args);
  } else {
    for (const param of fun.parameters)
      ary.push(args[param.name]);
  }
  // conversation is always in last position (validated in decorators)
  if (fun.conversation)
    ary.push(conv);
  return ary;
}

function isRequred(field: MetaField) {
  return (typeof field.options.optional === 'undefined' || !field.options.optional);
}
