import * as util from 'util';
import 'reflect-metadata'
import { AgentDefinition, AgentOptions, AnswerOptions, FieldOptions, getMetaStorage, MetaParameter, ObserverOptions, QuestionOptions, SkillRequestOptions, SkillOptions, ValueType } from './metadata';
import { extractArgs } from './helpers';
import { buildSkillRequest } from './outbound';

export function InputType() {
  return function (cnstr: Function) {
    getMetaStorage().defineInputType(cnstr.name, cnstr);
  }
}

export function OutputType() {
  return function (cnstr: Function) {
    getMetaStorage().defineOutputType(cnstr.name, cnstr);
  }
}

export function QuestionType() {
  return function (cnstr: Function) {
    getMetaStorage().defineOutputType(cnstr.name, cnstr);
  }
}

export function SkillType() {
  return function (cnstr: Function) {
    getMetaStorage().defineOutputType(cnstr.name, cnstr);
  }
}

export function StatusType() {
  return function (cnstr: Function) {
    getMetaStorage().defineOutputType(cnstr.name, cnstr);
  }
}

// Decorations that cannot be processed before the class decoration which is processed after all decorators
// inside the class.
var pendingDecorations: Record<string,Function[]> = {};

function addPending(cls: string, fn: (agent: AgentDefinition) => void) {
  if (pendingDecorations[cls])
    pendingDecorations[cls].push(fn);
  else
    pendingDecorations[cls] = [fn];
}

export function finalizeDecorations() {
  for (const cls in pendingDecorations)
    throw new Error(`Class ${cls} must have @Agent decorations`);
}

export function Agent(options: AgentOptions = {}) {
  return function (target: Function) {
    const cls = target.name;
    if (options.singleton && options.sessions)
      throw Error(`Invalid ${cls} agent options: Cannot use both singleton and sessions options`);
    const base = Object.getPrototypeOf(target).name || null;
    const agent = getMetaStorage().defineAgent(cls, target, base, options);
    if (pendingDecorations[cls]) {
      pendingDecorations[cls].forEach(fn => fn(agent));
      delete pendingDecorations[cls];
    }
  }
}

export function Field(options: FieldOptions = {}) {
  return function (target, propertyKey) {
    const fieldType: ValueType = options.type || Reflect.getMetadata('design:type', target, propertyKey);
    validateType(fieldType, (desc: string) => `Invalid type for ${target.constructor.name} property ${propertyKey}: ${desc}`);
    getMetaStorage().defineField(target.constructor.name, propertyKey, fieldType, options);
  }
}

export type ReturnTypeFunc = (returns?: void) => any;

export function Skill(options: SkillOptions) {
  return function (target, propertyKey, descriptor) {
    const cls = (typeof target === 'function') ? target.name : target.constructor.name;
    addPending(cls, (agent) => {
      if (agent) {
        if (agent.options.sessions) {
          if (typeof target !== 'function')
            throw new Error(`Skill method ${propertyKey} must be a static member for sessions agent ${cls}`);
        } else {
          if (typeof target === 'function')
            throw new Error(`Skill method ${propertyKey} must be a non-static member for singleton agent ${cls}`);
        }
      } else {
        throw new Error(`Class ${cls} must have @Agent decorator to use @Skill`);
      }
      const { parameters, conversation,requester } = collectParameters(target, propertyKey, descriptor);
      const returnType: Function = options.returns ? options.returns : Reflect.getMetadata('design:returntype', target, propertyKey);
      validateType(returnType, (desc: string) => `Invalid return type for ${cls}: ${desc}`, true);
      getMetaStorage().defineSkill(cls, {
        name: propertyKey,
        parameters,
        conversation,
        requester,
        returnType
      }, options, target[propertyKey]);  
    });
  }
}

export function Loader() {
  return function (target, propertyKey, descriptor) {
    if (typeof target !== 'function')
      throw new Error(`Loader method ${propertyKey} of ${target.constructor.name} must be a static member`)
    const returnType = Reflect.getMetadata('design:returntype', target, propertyKey);
    if (returnType) {
      if (returnType.name !== 'ConversationInstance')
        throw new Error(`Loader ${target.name} must return a ConversationInstance`);
    } else
      throw new Error(`Loader ${target.name} must return a ConversationInstance`);
    getMetaStorage().defineLoader(target.name, target[propertyKey]);
  }
}

export function Question(options: QuestionOptions) {
  return function (target, propertyKey, descriptor) {
    const { parameters, conversation, requester } = collectParameters(target, propertyKey, descriptor);
    const returnType: Function = options.returns ? options.returns : Reflect.getMetadata('design:returntype', target, propertyKey);
    validateType(returnType, (desc: string) => `Invalid return type for ${target.constructor.name}: ${desc}`);
    getMetaStorage().defineQuestion(target.constructor.name, {
      name: propertyKey,
      parameters,
      conversation,
      requester,
      returnType
    }, options, target[propertyKey]);
  }
}

export function Answer(options: AnswerOptions) {
  return function (target, propertyKey, descriptor) {
    const { parameters, conversation, requester } = collectParameters(target, propertyKey, descriptor);
    getMetaStorage().defineAnswer(target.constructor.name, {
      name: propertyKey,
      parameters,
      conversation,
      requester,
    }, options, target[propertyKey]);
  }
}

export function SkillRequest(options: SkillRequestOptions) {
  return function (target: any, propertyKey: string, descriptor: PropertyDescriptor) {
    const cls = (typeof target === 'function') ? target.name : target.constructor.name;
    const { parameters, conversation, requester } = collectParameters(target, propertyKey, descriptor);
    const skill = getMetaStorage().defineSkillRequest(cls, {
      name: propertyKey,
      parameters,
      conversation,
      requester,
    }, options, target[propertyKey]);
    descriptor.value = buildSkillRequest(skill);
  }
}

export function Observer(options: ObserverOptions) {
  return function (target, propertyKey, descriptor) {
    const cls = (typeof target === 'function') ? target.name : target.constructor.name;
    addPending(cls, (agent) => {
      if (agent) {
        if (agent.options.sessions) {
          if (typeof target !== 'function')
            throw new Error(`Observer method ${propertyKey} must be a static member for sessions agent ${cls}`);
        } else {
          if (typeof target === 'function')
            throw new Error(`Observer method ${propertyKey} must be a non-static member for singleton agent ${cls}`);
        }
      } else {
        throw new Error(`Class ${cls} must have @Agent decorator to use @Observer`);
      }
      const { parameters, conversation, requester } = collectParameters(target, propertyKey, descriptor);
      getMetaStorage().defineObserver(cls, {
        name: propertyKey,
        parameters,
        conversation,
        requester,
      }, options, target[propertyKey]);  
    });
  }
}

const specialParamTypes = ['Conversation', 'Requester'];

function collectParameters(target, propertyKey, descriptor) {
  const allParameters = extractMetaParameters(target, propertyKey, descriptor);
  const parameters = allParameters.filter(field => !specialParamTypes.includes(field.type.name));
  const nUser = parameters.length;
  var conversation = getSpecial('Conversation', allParameters, nUser);
  var requester = getSpecial('Requester', allParameters, nUser);
  return { parameters, conversation, requester };
}

function getSpecial(name: string, allParameters: MetaParameter[], nUser: number) {
  const idx = allParameters.findIndex(field => field.type.name === name);
  if (idx >= 0) {
    if (idx < nUser)
      throw new Error(`${name} prameter must appear after input parameters`);
    return allParameters[idx].name;
  }
}

function extractMetaParameters(target, propertyKey, descriptor): MetaParameter[] {
  const types: Function[] = Reflect.getMetadata(
    'design:paramtypes',
    target,
    propertyKey,
  );
  const names = extractArgs(descriptor.value);
  if (names.length > 0 && names[0].includes('{')) {
    var fields = [{
      name: '<anonymous>',
      destructured: true,
      type: types[0]
    }];
    var ti = types.length-1;
    var ni = names.length-1;
    while (ti >= 0 && ni >= 0) {
      if (specialParamTypes.includes(types[ti].name))
        fields.splice(1, 0, {
          name: names[ni],
          destructured: false,
          type: types[ti]
        });
      else
        break;
      ti -= 1;
      ni -= 1;
    }
    if (ti != 0) {
      throw new Error(`Failed to extract ${propertyKey} method parameters`)
    }
    return fields;
  } else {
    return names.map((k,idx) => {
      validateType(types[idx], (desc: string) => `Invalid type for ${propertyKey} parameter ${k}: ${desc}`);
      return {
        name: k,
        destructured: false,
        type: types[idx]
      };
    });
  }
}

function validateType(type: ValueType|undefined, formatError: (message: string) => string, nullable: Boolean = false) {
  if (type) {
    if (Array.isArray(type)) {
      if (type.length == 1)
        validateType(type[0], formatError);
      else
        throw new Error(formatError('array type should have 1 element'));
    } else {
      switch (type.name) {
        case 'Int':
        case 'Number':
        case 'String':
        case 'Boolean':
          break;
  
        case 'Array':
          throw new Error(formatError('cannot resolve array type, specify the type in options'))
  
        case 'Object':
        case 'Function':
          throw new Error(formatError('type must be one of number, string, boolean, or class'));
  
        default:
          if (!specialParamTypes.includes(type.name) && !getMetaStorage().getClass(type.name))
            throw new Error(formatError(`custom class ${type.name} must have decorator`));
          break;
      }    
    }  
  } else if (!nullable)
    throw new Error(formatError(`cannot be void type`));
}