import { AgentDefinition, AnswerDefinition, SkillDefinition, getMetaStorage, isArrayType, isScalarType } from './metadata';
import { askEventName, responseEventName, statusEventName } from './paraflow';
import { postParaflowEvent } from './paraflow-client';

var conversationInstances: Record<string,Conversation> = {};

export function getOrCreateConversation(id: string, agent: AgentDefinition, skill: SkillDefinition, answers: AnswerDefinition[]) {
  if (conversationInstances[id])
    return conversationInstances[id];
  var conv = new Conversation(id)
  conv.construct(agent, skill, answers);
  conversationInstances[id] = conv;
  return conv;
}

export function getConversation(cls: string, id: string) {
  if (!conversationInstances[id]) {
    const loader = getMetaStorage().getLoader(cls);
    if (loader) {
      console.log(`Attempt to hyrdrate missing ${cls} conversation ${id}`);
      var conv = new Conversation(id)
      const loaded = loader.apply(null, [conv]) as ConversationInstance<any>;
      if (loaded) {
        const agent = getMetaStorage().getAgent(cls);
        const skills = getMetaStorage().getSkills().filter(skill => skill.cls == cls && skill.options.action === loaded.action);
        const answers = getMetaStorage().getAnswers().filter(ans => ans.cls ==cls && ans.options.action == loaded.action);
        if (skills.length == 1) {
          conv.construct(agent, skills[0], answers);
          conv.instance = loaded.agent;
          conversationInstances[id] = conv;
          console.log(`Conversation ${id} restored`);
        }
      }
    } else {
      console.error(`Missing ${cls} conversation ${id}`);
    }
  }
  return conversationInstances[id];
}

export class ConversationInstance<T> {
  subject: string;
  action: string;
  agent: T;
}

export class Conversation {
  id: string;
  recipientId: string;
  agent: AgentDefinition;
  skill: SkillDefinition;
  asks: Record<string,AnswerDefinition>;
  instance?: object;

  constructor(id: string) {
    this.id = cleanId(id);
    this.recipientId = id;
  }

  construct(agent: AgentDefinition, skill: SkillDefinition, answers: AnswerDefinition[]) {
    this.agent = agent;
    this.skill = skill;
    this.asks = Object.fromEntries(answers.map(ans => [ans.fun.name, ans]));
  }

  async ask(fun: Function, value: Record<string,any>) {
    if (this.asks[fun.name]) {
      var body = Object.assign({}, value);
      body['_cid'] = this.recipientId;
      await postParaflowEvent(askEventName(this.agent, this.asks[fun.name]), body);  
    } else {
      throw new Error(`Ask made for unknown answer function ${fun.name}`)
    }
  }

  async status(type: Function, value: Record<string,any>) {
    var body = Object.assign({}, value);
    body['_cid'] = this.recipientId;
    await postParaflowEvent(statusEventName(this.agent, type.name), body);  
  }

  async response(value: any) {
    var body: Record<string,any>;
    if (this.skill.fun.returnType) {
      if (isScalarType(this.skill.fun.returnType) || isArrayType(this.skill.fun.returnType)) {
        const result = (value !== undefined) ? value : true;
        body = { result };
      } else
        body = value;
    } else {
      body = { result: true };
    }
    body['_cid'] = this.recipientId;
    await postParaflowEvent(responseEventName(this.agent, this.skill), body);
    delete conversationInstances[this.recipientId];
  }
}

function cleanId(id: string) {
  return id.split('@')[0];
}
