import * as stream from 'stream';

export function CsvSource(istream: stream.Readable): stream.Transform {
  return istream.pipe(new CsvTransform());
}

class CsvTransform extends stream.Transform {
  lineno: number;
  buffer: string;
  schema: string[];

  constructor() {
    super({ writableObjectMode: false, readableObjectMode: true});
    this.buffer = '';
    this.lineno = 0;
  }

  _transform(chunk: any, enc: string, callback: Function) {
    this.buffer += chunk;
    var nl = this.buffer.indexOf('\n');
    while (nl >= 0) {
      const line = this.buffer.substring(0, nl);
      this.buffer = this.buffer.substring(nl+1);
      this.emitLine(line);
      nl = this.buffer.indexOf('\n');
    }
    callback(null);
  }

  _flush(callback: Function) {
    if (this.buffer) {
      const line = this.buffer;
      this.buffer = '';
      this.emitLine(line);
    }
    callback(null);
  }

  emitLine(line: string) {
    var row = parseRow(line);
    this.lineno += 1;
    if (this.lineno > 1) {
      var obj = {};
      for (var i in this.schema) {
        obj[this.schema[i]] = row[i];
      }
      this.push(obj);
    } else {
      this.schema = row;
      console.log('Source header', row);
    }
  }
}

function parseRow(str: string): string[] {
  var arr = [];
  var quote = false;
  var col = 0;
  for (var c = 0; c < str.length; c++) {
      var cc = str[c], nc = str[c+1];
      arr[col] = arr[col] || '';

      if (quote) {
        if (cc == '"' && nc == '"') { arr[col] += cc; ++c; continue; }
        if (cc == '"') { quote = false; continue; }
      } else {
        if (cc == '"') { quote = true; continue; }
        if (cc == ',') { ++col; continue; }
        if (cc == '\n' || cc == '\r') { break; }
      }
      
      arr[col] += cc;
  }
  return arr;
}