import { isObjectParameter, isVoidType, SkillRequestDefinition } from "./metadata";
import { outboundRequestEventName } from "./paraflow";
import { paraflowClient } from "./paraflow-client";

interface ResponseCallback {
  accept: Function;
  reject: Function;
}

var pending: Record<string,ResponseCallback> = {};

export function makeRequest<T>(): Promise<T> {
  return Promise.reject(new Error("Unexpected requestSkill call.  Caller must have @SkillRequest decoration."));
}

// Generates the client-side skill request function which posts the Paraflow event.
// The response will come back through the GraphQL API.

export function buildSkillRequest(skill: SkillRequestDefinition) {
  const eventName = outboundRequestEventName(skill);
  const noresponse = isVoidType(skill.options.returns);
  const name = `${skill.options.subject}/${skill.options.action}`;
  const argNames = skill.fun.parameters.map(param => param.name);
  const objParam = isObjectParameter(skill.fun.parameters);
  return async (...args: any[]) => {
    const body = objParam ? args[0] : buildArgs(args, argNames);
    const resp = await paraflowClient.postEvent(eventName, body);
    if (!noresponse) {
      console.log(`Skill request ${resp.data.uid}: ${plainStringify(body)} -> ${name}`)
      const answer = await waitForResponse(resp.data.uid);
      console.log(`Skill response ${resp.data.uid}: ${name} -> ${plainStringify(answer)}`)
      return answer;
    }
    console.log(`Skill request (fire-n-forget): ${name}(${plainStringify(body)})`)
  };
}

// Call from GraphQL API when the response is received
export function handleSkillResponse(uid: string, body: object) {
  if (pending[uid]) {
    pending[uid].accept(body);
  }
}

function buildArgs(args: any[], argNames: string[]) {
  var body = {};
  for (var i in argNames)
    body[argNames[i]] = args[i];
  return body;
}

function waitForResponse(uid: string) {
  return new Promise((accept, reject) => {
    pending[uid] = { accept, reject };
  });
}

function plainStringify(obj: any) {
  if (typeof obj !== "object" || Array.isArray(obj))
    return JSON.stringify(obj);

  const props: string = Object.entries(obj)
    .map(([key,value]) => `${key}: ${plainStringify(value)}`)
    .join(', ');
  return props;
}
