import * as fs from 'fs';
import * as url from 'url';
import * as path from 'path';
import * as http from 'http';
import { createHandler } from 'graphql-http/lib/use/http';

import { generateParaflow } from "./paraflow";
import { generateSchema } from './schema';
import { initParaflowClient, paraflowClient } from './paraflow-client';
import { finalizeDecorations } from './decorators';
import { getMetaStorage } from './metadata';

const PROJ_PATH = path.join(getScriptDir(), '../');
const DEV_MODE = !!process[Symbol.for("ts-node.register.instance")];

var compileMode = false;
var called = false;

export interface RunAgentOptions {
  graphiqlEn?: boolean;
  testdrive?: boolean;
  port?: number;
}

const defaultOptions = { graphiqlEn: true, testdrive: true };

export function setCompileMode() {
  compileMode = true;
}

export async function startAgent(opt? : RunAgentOptions);
export async function startAgent(inst: Object, opt? : RunAgentOptions);
export async function startAgent(instOrOpt: Object|RunAgentOptions = defaultOptions, opt : RunAgentOptions = defaultOptions) {
  called = true;

  finalizeDecorations();

  const hasInst = !('graphiqlEn' in instOrOpt || 'testdrive' in instOrOpt);
  const inst = (hasInst) ? instOrOpt : null;
  var opts: RunAgentOptions = Object.assign({}, defaultOptions);
  if (hasInst)
    Object.assign(opts, opt);
  else
    Object.assign(opts, instOrOpt);


  if (compileMode) {
    generateParaflow();
    return;
  }

  const agents = getMetaStorage().getAgents().filter(agent => !agent.options.abstract);
  if (agents.length == 1) {
    if (agents[0].options.sessions) {
      if (hasInst)
        throw new Error(`Do not call startAgent with an class instance for session agent.`);
    } else {
      if (!hasInst)
        throw new Error(`Must call startAgent with a ${agents[0].cls} instance for singleton agent.`);
    }
  } else {
    throw new Error('Expecting one top-level agent class');
  }

  initParaflowClient();

  if (DEV_MODE && opts.testdrive) {
    const filename = path.join(PROJ_PATH, 'ts_agent.paraflow');
    if (generateParaflow(filename)) {
      console.log('Deploying agent', filename);
      await paraflowClient.deployTestdrive(filename);
      console.log('Agent deployed');
    } else {
      console.log('Agent not found.  Please add the @Agent decoration to your agent class.')
    }
  }

  const schema = generateSchema(inst);

  const graphiqlHTML = opts.graphiqlEn ? fs.readFileSync(path.join(PROJ_PATH, '/graphiql/index.html')) : null;
  
  const handler = createHandler({ schema });
  const server = http.createServer((req, res) => {
    if (opts.graphiqlEn && req.url === '/') {
      res.writeHead(200, {"Content-Type": "text/html"});
      res.write(graphiqlHTML);
      res.end();
    } else if (req.url.startsWith('/graphql')) {
      handler(req, res);
    } else {
      res.writeHead(404).end();
    }
  });

  const port = opts.port||process.env['AGENT_PORT']||4000;

  server.listen(port);
  console.log('Listening to port', port);

  if (inst && typeof inst['started'] == 'function')
    inst['started']();

  return inst;
}

// Handle esm/commonjs interop
function getScriptDir() {
  var here = __dirname;
  // For esm, above is replaced with import.meta.url
  if (here.startsWith('file:'))
    here = path.dirname(url.fileURLToPath(here));
  return here;
}

process.on('exit', function() {
  if (!called)
    console.log("Don't forget to call startAgent!");
});