import * as fs from 'fs';
import * as util from 'util';
import axios, { AxiosInstance } from 'axios';

const PARAFLOW_HOST = process.env['PARAFLOW_HOST'] || 'http://localhost:3030';
const MAX_RETRY = 5;

export var paraflowClient: ParaflowClient = null;

export function initParaflowClient() {
  paraflowClient = new ParaflowClient(PARAFLOW_HOST);
}

export async function postParaflowEvent(name: string, body: Record<string,any>) {
  await paraflowClient.postEvent(name, body);
}

export class ParaflowClient {
  protected readonly instance: AxiosInstance;

  public constructor(baseURL: string) {
    this.instance = axios.create({
      baseURL,
    });
  }

  public async goalDefs() {
    const result = await this.instance.get('/_debug/goals');
    return result['data'];
  }

  public async postEvent(name: string, body: Record<string,any>) {
    var retries = 0;
    var backoff = 1000;
    const path = `/event/${name}`;
    console.log('POST', path);
    while (retries < MAX_RETRY) {
      try {
        const result = await this.instance.post(path, body);
        return result['data'];
      } catch (err) {
        if (err.response) {
          if (err.response.status !== 409) {
            console.log(err.response.status, err.response.statusText);
            console.log(util.inspect(err.response.data, {depth: null}));
            return;  
          }
          // may need time for Paraflow goal to be planned and create enablements
          await sleep(backoff);
          if (backoff < 4000)
            backoff *= 2;
          retries += 1;
        } else {
          console.log(err);
          return;
        }
      }
    }
  }

  public async deployTestdrive(filename: string) {
    const src = fs.readFileSync(filename);
    // Validate syntax before deploying
    var response = await this.instance.post('/_debug/parse', src);
    if (response.data === 'OK') {
      console.log('Syntax check OK');
    } else {
      throw Error(`Syntax check FAIL: ${response.data}`);
    }
    await this.instance.post('_debug/source/paraflow', src);
    await this.instance.post('_debug/shutdown', '');

    // Wait for Paraflow to restart before continuing
    for (var i = 0; i < 15; i++) {
      try {
        await this.goalDefs();
        break;
      } catch (err) {
      }
      await sleep(1000);
    }
  }
}

async function sleep(ms: number) {
  return new Promise((accept, reject) => {
    setTimeout(accept, ms);
  });
}