import * as fs from 'fs';
import * as util from 'util';
import { AgentDefinition, AnswerDefinition, ClassDefinition, FieldOptions, MetaField, MetaFunction, MetaParameter, ObserverDefinition, QuestionDefinition, SkillRequestDefinition, SkillDefinition, UiCard, ValueType, getMetaStorage, isArrayType, isObjectType, isScalarType, isObjectParameter, isVoidType } from './metadata';
import { JsonValue } from './types';

export function generateParaflow(filename?: string) {
  const metadata = getMetaStorage();
  
  var hierarchy: Record<string,string> = {};
  var bases = new Set<string>();
  metadata.getAgents().forEach(agent => {
    if (agent.base) {
      hierarchy[agent.cls] = agent.base;
      bases.add(agent.base);
    }
  });

  const leaves = metadata.getAgents().filter(agent => !agent.options.abstract).map(agent => agent.cls);
  if (leaves.length > 1)
    throw new Error('Multiple agent classes not supported: ' + leaves.join(', '));
  else if (leaves.length == 0)
    throw new Error('No agent class defined');

  for (const cls of leaves) {
    const agent = metadata.getAgent(cls);
    if (!agent)
      throw new Error(`Class ${cls} needs @Agent decoration`);

    var code = '';
    for (const cls1 of lineage(cls, hierarchy)) {
      code += generateForClass(agent, cls1);
    }

    if (!filename)
      filename = `${cls}.paraflow`;
    fs.writeFileSync(filename, code);

    return filename;
  }
}

function lineage(cls: string, hierarchy: Record<string,string>) {
  if (hierarchy[cls]) {
    var ancestry = lineage(hierarchy[cls], hierarchy);
    ancestry.push(cls);
    return ancestry;
  } else {
    return [cls];
  }
}

// Generate all interfaces registered for cls
// The agent may be a super class of cls
function generateForClass(agent: AgentDefinition, cls: string) {
  const metadata = getMetaStorage();

  var code = '';

  const skills = metadata.getSkills().filter(skill => skill.cls === cls);
  for (const skill of skills) {
    code += generateSkill(agent, skill);
  }

  const provider = skills.find(skill => metadata.isKeyProvider(skill));
  const destroy = skills.find(skill => metadata.isKeyDestroyer(skill));
  if (provider && !destroy) {
    // need to declare an event so Paraflow treats the complete goal as opaque
    var lines = [];
    const key = metadata.getProviderKey(provider.fun);
    lines.push(`event terminate${cls}($${key.name} ${toParaflowType(key.type)}) {`);
    lines.push(`  assert !${actorCompleteName(provider)}($${key.name});`);
    lines.push('}\n');
    code += lines.join('\n');
  }

  const answers = metadata.getAnswers().filter(ans => ans.cls == cls);
  for (const ans of answers) {
    code += generateAsk(agent, ans);
  }

  const questions = metadata.getQuestions().filter(qu => qu.cls === cls);
  for (const qu of questions) {
    code += generateQuestion(agent, qu);
  }

  if (agent.options.status)
    for (const status of agent.options.status)
      code += generateStatus(agent, status);

  const observers = metadata.getObservers().filter(qu => qu.cls === cls);
  for (const obs of observers) {
    code += generateObserver(agent, obs);
  }
    
  const responses = metadata.getSkillRequests().filter(resp => resp.cls == cls);
  for (const resp of responses) {
    code += generateSkillRequest(agent, resp);
  }

  return code;
}

function generateQuestion(agent: AgentDefinition, qu: QuestionDefinition) {
  var lines = [];

  const meta = getMetaStorage();

  const id = qu.options.id || qu.fun.name;
  const eventName = questionEventName(agent, qu);
  const eventParams = eventParameters(qu.fun.parameters);

  var store = VirtualStore.fromParameters(qu.fun.parameters);
  const taskVars = taskParameters(qu.fun.parameters, store);

  const taskName = questionTaskName(agent, qu);

  ////////// Question event

  lines.push(`%question(id=${id},subject=${agent.options.subject},action=${qu.options.action})`);
  
  if (qu.options.form)
    lines.push(generateForm(qu.options.form));
  generateDisplay(qu.options.displayQuestion, qu.options.displayAnswer, lines);

  const eventParamsStr = strJoin(eventParams.join(', '), '$conversation string, $messageid string');
  lines.push(`event ${eventName}(${eventParamsStr}) {`);
  
  store.generateParameterExpansion(lines);
  
  const taskArgsStr = strJoin(taskVars.join(', '), '_cid -> $conversation, _mid -> $messageid');
  lines.push(`  !${taskName}(${taskArgsStr});`);

  lines.push('}\n');

  ////////// Question task

  const taskParamsStr = strJoin(taskVars.join(', '), '$_cid, $_mid');
  const importClause = generateImportClause(agent, qu.fun, meta.getKeyIn(qu.fun));

  lines.push(`task !${taskName}(${taskParamsStr})${importClause} on Env("ACTOR_ENDPOINT") {`)

  var taskStore = store.toGoalStore();
  const callArgsStr = strJoin(makeCallParamMap(qu.fun.parameters, taskStore), '$_cid, $_mid');
  const call = `@${qu.fun.name}(${callArgsStr})`;

  if (qu.fun.returnType) {
    const outStore = VirtualStore.fromType('result', qu.fun.returnType);
    outStore.generateAssign(call, lines);

    // could all be hidden
    const response = makeResponse(qu.fun.returnType, outStore);
    lines.push(`  pncp answer(${response});`);
  } else {
    throw new Error(`Invalid question member ${qu.fun.name}: must return a value`);
  }

  lines.push('}\n');

  return lines.join('\n') + '\n';
}

function generateAsk(agent: AgentDefinition, ans: AnswerDefinition) {
  var lines = [];

  const id = ans.options.id || ans.fun.name;

  // Mock a destructured function input to reuse helpers expecting a parameter list
  var parameters = [];
  if (ans.options.question)
    parameters.push({
      name: '<anonymous>',
      destructured: true,
      type: ans.options.question
    });

  const meta = getMetaStorage();

  const eventName = askEventName(agent, ans);
  const eventParams = eventParameters(parameters);

  var askStore = VirtualStore.fromParameters(parameters);
  const taskVars = taskParameters(parameters, askStore);

  const taskName = askTaskName(agent, ans);

  ////////// Ask event
  
  const eventParamsStr = strJoin(eventParams.join(', '), '$_cid string');
  lines.push(`event ${eventName}(${eventParamsStr}) {`);
  askStore.generateParameterExpansion(lines);
  
  const taskArgsStr = strJoin(taskVars.join(', '), '$_cid');
  lines.push(`  !${taskName}(${taskArgsStr});`);

  lines.push('}\n');

  
  ////////// Ask task

  const taskParamsStr = strJoin(taskVars.join(', '), '$_cid');
  const importClause = generateImportClause(agent, ans.fun, meta.getKeyIn(ans.fun));

  lines.push(`%ask(id=${id},subject=${agent.options.subject},action=${ans.options.action})`);
  if (ans.options.questionForm)
    lines.push(generateForm(ans.options.questionForm));
  generateDisplay(ans.options.displayQuestion, ans.options.displayAnswer, lines);

  lines.push(`task !${taskName}(${taskParamsStr})${importClause} is`)

  var taskStore = askStore.toGoalStore();
  const askArgsStr = makeCallParamMap(parameters, taskStore);
  const ask = `ask ${id} (${askArgsStr}) to $_cid`;

  var store = VirtualStore.fromParameters(ans.fun.parameters);
  store.generateAssign(ask, lines, '  ', 'pncp ', ' in {');

  const callArgs = strJoin(makeCallParamMap(ans.fun.parameters, store), '$_cid');
  const call = `@${ans.fun.name}(${callArgs})`;
  lines.push(`    ${call} on Env("ACTOR_ENDPOINT");`);
  
  lines.push('  }\n');

  return lines.join('\n') + '\n';
}

function generateStatus(agent: AgentDefinition, type: Function) {
  var lines = [];

  // Mock a destructured function input to reuse helpers expecting a parameter list
  const parameters = [{
    name: '<anonymous>',
    destructured: true,
    type
  }];

  const eventParams = eventParameters(parameters);
  const store = VirtualStore.fromParameters(parameters);

  const taskVars = taskParameters(parameters, store);
  const taskName = statusTaskName(agent, type.name);

  ////////// Status event

  const eventParamsStr = strJoin(eventParams.join(', '), '$_cid string');
  lines.push(`event ${statusEventName(agent, type.name)}(${eventParamsStr}) {`);
  store.generateParameterExpansion(lines);  

  const taskArgsStr = strJoin(taskVars.join(', '), '$_cid');
  lines.push(`  !${taskName}(${taskArgsStr});`);

  lines.push('}\n');

  ////////// Status task

  const taskParamsStr = strJoin(taskVars.join(', '), '$_cid');
  lines.push(`task !${taskName}(${taskParamsStr}) {`)

  var taskStore = store.toGoalStore();
  const argsStr = makeCallParamMap(parameters, taskStore);
  lines.push(`  pncp status(${argsStr}) to $_cid;`);

  lines.push('}\n');

  return lines.join('\n');
}

function generateObserver(agent: AgentDefinition, obs: ObserverDefinition) {
  var lines = [];

  const meta = getMetaStorage();

  const eventName = observerEventName(obs);
  const eventParams = eventParameters(obs.fun.parameters);

  var store = VirtualStore.fromParameters(obs.fun.parameters);
  const taskVars = taskParameters(obs.fun.parameters, store);

  const taskName = observerTaskName(obs);

  ////////// Observer event

  lines.push(`%observer(subject=${obs.options.subject},action=${obs.options.action},message_types=skill)`);
  
  const eventParamsStr = strJoin(eventParams.join(', '), '$conversation string');
  lines.push(`event ${eventName}(${eventParamsStr}) {`);
  
  store.generateParameterExpansion(lines);
  
  const taskArgsStr = strJoin(taskVars.join(', '), '_cid -> $conversation');
  lines.push(`  !${taskName}(${taskArgsStr});`);

  lines.push('}\n');

  ////////// Observer task

  const taskParamsStr = strJoin(taskVars.join(', '), '$_cid');
  const importClause = generateImportClause(agent, obs.fun, meta.getKeyIn(obs.fun));

  lines.push(`task !${taskName}(${taskParamsStr})${importClause} on Env("ACTOR_ENDPOINT") {`)

  var taskStore = store.toGoalStore();
  const callArgsStr = strJoin(makeCallParamMap(obs.fun.parameters, taskStore), '$_cid');
  lines.push(`  @${obs.fun.name}(${callArgsStr});`);

  lines.push('}\n');

  return lines.join('\n') + '\n';
}

function generateSkill(agent: AgentDefinition, skill: SkillDefinition) {
  const meta = getMetaStorage();

  const isRoot = meta.isKeyProvider(skill);
  const inputKey = meta.getKeyIn(skill.fun);
  const outputKey = meta.getKeyOut(skill.fun);
  const passConversation = !!skill.fun.conversation || skill.options.background;

  console.log(`Generating skill ${agent.options.subject}/${skill.options.action}`);
  reportInputs(skill.fun.parameters, inputKey)
  if (skill.fun.returnType)
    reportOutputs(skill.fun.returnType, outputKey)
  console.log('\n');

  var lines = [];

  lines.push(`%skill(subject=${agent.options.subject},action=${skill.options.action})`);
  if (skill.options.form)
    lines.push(generateForm(skill.options.form));
  generateDisplay(skill.options.displayRequest, skill.options.displayResponse, lines);

  const eventName = requestEventName(agent, skill);

  var params = eventParameters(skill.fun.parameters);
  var store = VirtualStore.fromParameters(skill.fun.parameters);
  //console.log(util.inspect(store, {depth:null}));

  const taskName = requestTaskName(skill);
  var taskParams = taskParameters(skill.fun.parameters, store);

  params.push('$conversation string');

  lines.push(`event ${eventName}(${params.join(', ')}) {`);

  // Create a uid if input key is auto or output key (generated by a return value) or background and no key
  const uid = !!outputKey ? '_uid' : (inputKey && inputKey.options.key === 'auto') ? inputKey.name : null;
  if (uid)
    lines.push(`  let $${uid} = Uuid();`);

  // Unpack any nested-object parameters
  store.generateParameterExpansion(lines);

  var entryParams = taskParams.join(', ');
  if (!!outputKey)
    entryParams = strJoin(`$${uid}`, entryParams);
  var entryArgs = entryParams;
  
  // Always include _cid to make goals unique
  entryParams = strJoin(entryParams, '$_cid');
  entryArgs = strJoin(entryArgs, '_cid -> $conversation');
  taskParams.push('$_cid');

  if (isRoot || skill.options.background) {
    lines.push(`  !${requestEntryName(skill)}(${entryArgs});`)
  } else
    lines.push(`  !${taskName}(${entryArgs});`)

  lines.push('}\n');

  if (isRoot) {
    // If the real key is returned by the entry task then a temporary key is used to launch
    // the instance, otherwise the launch key is the actual key.
    const launchKey = uid || inputKey?.name;
    const realKey = outputKey ? outputKey.name : launchKey;

    const consumers = meta.getKeyConsumers(skill.cls, realKey);
    const enables = questionEnables(agent, skill).concat(consumers.map(c => `${requestEventName(agent, c)}(${realKey} -> $${realKey})*`));

    if (launchKey !== realKey) {
      // Need to split rule to capture the real key
      lines.push(`rule !${requestEntryName(skill)}(${entryParams}) {`);
      lines.push(`  !${taskName}(${taskParams.join(', ')});`)
      lines.push(`  !${actorProcessName(skill)}($${launchKey});`);
      lines.push('}\n');

      const keyClause = outputKey ? `, !${taskName}(${uid} == $${uid}, $${realKey})` : '';
      lines.push(`rule !${actorProcessName(skill)}($${launchKey})${keyClause} {`);
      lines.push(`  !${actorCompleteName(skill)}($${realKey}), ${enables.join(', ')};`);
      lines.push('}\n');
    } else {
      lines.push(`rule !${requestEntryName(skill)}(${entryParams}) {`);
      lines.push(`  !${taskName}(${taskParams.join(', ')});`)
      lines.push(`  !${actorCompleteName(skill)}($${realKey}), ${enables.join(', ')};`);
      lines.push('}\n');
    }
  } else if (skill.options.background) {
    const enables = questionEnables(agent, skill).concat([`${responseEventName(agent, skill)}(_cid -> $_cid)`]);
    lines.push(`rule !${requestEntryName(skill)}(${entryParams}) {`);
    lines.push(`  !${taskName}(${taskParams.join(', ')});`)
    lines.push(`  !${actorCompleteName(skill)}($_cid), ${enables.join(', ')};`);
    lines.push('}\n');
  }

  const importClause = generateImportClause(agent, skill.fun, inputKey);

  lines.push(`task !${taskName}(${taskParams.join(', ')})${importClause} on Env("ACTOR_ENDPOINT") {`)

  var taskStore = store.toGoalStore();
  var callArgs = makeCallParamMap(skill.fun.parameters, taskStore);
  if (passConversation)
    callArgs = strJoin(callArgs, '$_cid');
  const call = `@${skill.fun.name}(${callArgs})`;

  if (skill.fun.returnType && ! skill.options.background) {
    const outStore = VirtualStore.fromType('result', skill.fun.returnType);
    outStore.generateAssign(call, lines);

    // could all be hidden
    const response = makeResponse(skill.fun.returnType, outStore);
    if (response) {
      lines.push(`  pncp response(${response});`);
    } else {
      lines.push(`  pncp response();`);
    }

    if (isRoot || skill.options.background) {
      const saved = makeReturn('result', skill.fun.returnType, outStore);
      if (saved)
        lines.push(`  return ${saved};`);  
    }
  } else {
    lines.push(`  ${call};`);
    if (! skill.options.background)
      lines.push(`  pncp response();`); // no return
  }

  lines.push('}');
  lines.push('');

  if (skill.options.background) {
    const respStore = skill.fun.returnType ? VirtualStore.fromType('result', skill.fun.returnType) : new VirtualStore();

    var respParams = skill.fun.returnType ? responseParameters(skill.fun.returnType) : [];
    respParams.push('$_cid string');
    const respArgs = respParams.map(arg => arg.split(' ')[0]);

    ////////// response event

    lines.push(`event ${responseEventName(agent, skill)}(${respParams.join(', ')}) {`)
    respStore.generateParameterExpansion(lines);

    lines.push(`  !${responseTaskName(agent, skill)}(${respArgs.join(', ')});`);

    const completeKey = isRoot ? meta.getProviderKey(skill.fun) : '_cid';
    lines.push(`  assert !${actorCompleteName(skill)}($${completeKey});`);

    lines.push('}\n');

    ////////// response task

    lines.push(`task !${responseTaskName(agent, skill)}(${respArgs.join(', ')}) {`);
    if (skill.fun.returnType) {
      // could all be hidden
      const response = makeResponse(skill.fun.returnType, respStore);
      if (response) {
        lines.push(`  pncp response(${response}) to $_cid;`);
      } else {
        lines.push(`  pncp response() to $_cid;`);
      }
    } else {
      lines.push(`  pncp response() to $_cid;`); // no return
    }

    lines.push('}\n');
  }

  return lines.join('\n') + '\n';
}

function generateSkillRequest(agent: AgentDefinition, skill: SkillRequestDefinition) {
  var lines = [];

  const meta = getMetaStorage();

  const eventName = outboundRequestEventName(skill);
  const eventParams = eventParameters(skill.fun.parameters);

  var store = VirtualStore.fromParameters(skill.fun.parameters);
  const taskVars = taskParameters(skill.fun.parameters, store);

  const taskName = outboundRequestTaskName(skill);

  ////////// Skill request event

  const eventParamsStr = eventParams.join(', ');
  lines.push(`event ${eventName}(${eventParamsStr}) {`);
  
  store.generateParameterExpansion(lines);
  lines.push(`  let $uid = Uuid();`);

  const taskArgsStr = strJoin(taskVars.join(', '), '_uid -> $uid');
  lines.push(`  !${taskName}(${taskArgsStr});`);

  lines.push(`  return { $uid };`)

  lines.push('}\n');

  ////////// Skill request task

  const taskParamsStr = strJoin(taskVars.join(', '), '$_uid');


  const reqArgsStr = makeCallParamMap(skill.fun.parameters, store);
  const req = `${skill.options.subject}/${skill.options.action}(${reqArgsStr})`;

  if (isVoidType(skill.options.returns)) {
    lines.push(`task !${taskName}(${taskParamsStr}) {`)
    lines.push(`  pncp request ${req};`)
    lines.push('}\n');
  } else {
    lines.push(`task !${taskName}(${taskParamsStr}) is`)

    const outStore = VirtualStore.fromType('result', skill.options.returns);
    outStore.generateAssign(req, lines, '  ', 'pncp ', ' in {');
  
    const callArgs = strJoin(makeResponse(skill.options.returns, outStore), '$_uid');
    lines.push(`    @${skill.fun.name}(${callArgs}) on Env("ACTOR_ENDPOINT");`);
  
    lines.push('  }\n');  
  }

  return lines.join('\n') + '\n';
}


function generateForm(card: UiCard) {
  return `%form(${JSON.stringify(card)})`;
}

function generateDisplay(displayIn: UiCard|undefined, displayOut: UiCard|undefined, lines: string[]) {
  if (displayIn)
    lines.push(`%display_in(${JSON.stringify(displayIn)})`);
  if (displayOut)
    lines.push(`%display_out(${JSON.stringify(displayOut)})`);
}

function questionEnables(agent: AgentDefinition, skill: SkillDefinition) {
  const questions = getMetaStorage().getQuestions().filter(qu => qu.cls === skill.cls && qu.options.action ===skill.options.action);
  var enables = questions.map(qu => `${questionEventName(agent, qu)}(conversation -> $_cid)*`);
  
  const answers = getMetaStorage().getAnswers().filter(ans => ans.cls === skill.cls && ans.options.action ===skill.options.action);
  enables = enables.concat(answers.map(ans => `${askEventName(agent, ans)}(_cid -> $_cid)*`));

  const statuses = agent.options.status || [];
  enables = enables.concat(statuses.map(type => `${statusEventName(agent, type.name)}(_cid -> $_cid)*`));

  return enables;
}

// Generates the typed parameter definitions of the event.
// Although rules won't accept table arguments, we want to accept tables on the event
// so that the skill input type can be determined from the event.

function eventParameters(params: MetaParameter[]) {
  if (isObjectParameter(params)) {
    const type = params[0].type;
    const cls = getMetaStorage().getRequiredClass(type.name);
    return Object.keys(cls.fields)
      .filter(field => ! cls.fields[field].options.restore && cls.fields[field].options.key !== 'auto')
      .map(field => {
        var type = toParaflowType(cls.fields[field].type);
        if (cls.fields[field].options.optional)
          type = 'optional ' + type;
        return `$${field} ${type}`;
      });
  } else {
    return params.map(param => {
      const type = toParaflowType(param.type);
      return `$${param.name} ${type}`;
    });
  }
}

function taskParameters(params: MetaParameter[], store: VirtualStore) {
  const imported = getImported(params);
  const excluded = new Set(imported.map(name => '$' + name));
  return Object.keys(store.types).filter(name => !excluded.has(name));
}

function generateImportClause(agent: AgentDefinition, fun: MetaFunction, inputKey?: MetaField) {
  var importClause = '';

  if (inputKey && inputKey.options.key === 'active') {
    const providers = getMetaStorage().getKeyProviders(agent.cls);
    if (providers.length == 1) {
      const imports = getImported(fun.parameters).map(name => '$' + name);
      if (imports.length > 0) {
        const cond = `${inputKey.name} == $${inputKey.name}`;
        const task = providers[0].options.background ? responseTaskName(agent, providers[0]) : requestTaskName(providers[0]);
        importClause = `, !${task}(${cond}, ${imports.join(', ')})`;
      }
    } else
      throw new Error(`Input ${inputKey.name} has ${providers.length} key providers`);
  }
  
  return importClause;
}

function getImported(params: MetaParameter[]) {
  if (isObjectParameter(params)) {
    const type = params[0].type;
    const cls = getMetaStorage().getRequiredClass(type.name);
    return Object.keys(cls.fields).filter(field => !! cls.fields[field].options.restore);
  }
  return [];
}

function responseParameters(type: ValueType) {
  if (isScalarType(type) || Array.isArray(type)) {
    return [`$result ${toParaflowType(type)}`];
  } else {
    const cls = getMetaStorage().getTypeClass(type);
    return Object.keys(cls.fields)
      .map(field => {
        const type = toParaflowType(cls.fields[field].type);
        return `$${field} ${type}`;
      });
  }
}

class VirtualStore {
  virtual: Record<string,VirtualMap>;
  types: Record<string,ValueType>;
  scalar?: string;

  constructor() {
    this.virtual = {};
    this.types = {};
  }

  static fromType(name: string, type: ValueType) {
    var store = new VirtualStore();
    if (isScalarType(type)) {
      store.scalar = '$'+name;
      store.types[store.scalar] = type;
    } else if (Array.isArray(type)) {
      store.scalar = '$'+name;
      store.types[store.scalar] = type;
    } else {
      const cls = getMetaStorage().getRequiredClass(type.name);
      for (const field in cls.fields) {
        store.virtual[field] = store.makeVirtual('', field, cls.fields[field].type);
      }      
    }
    return store;
  }

  clone(): VirtualStore {
    var types = {};
    for (const name in this.types) {
      types[name] = this.types[name];
    }
    const virtual = this.cloneMapping(this.virtual);
    const scalar = this.scalar;
    return Object.assign(new VirtualStore(), { virtual, types, scalar });
  }

  private cloneMapping(mapping: VirtualObj) {
    var clone = {};
    for (const name in mapping) {
      const local = mapping[name];
      if (typeof local === 'string') {
        clone[name] = local;
      } else {
        clone[name] = this.cloneMapping(local);
      }
    }
    return clone;
  }

  static fromParameters(params: MetaParameter[], excluded: Set<string> = new Set()) {
    var store = new VirtualStore();
    if (isObjectParameter(params)) {
      const type = params[0].type;
      const cls = getMetaStorage().getRequiredClass(type.name);
      for (const field in cls.fields) {
        if (!excluded.has(field))
          store.virtual[field] = store.makeVirtual('', field, cls.fields[field].type);
      }
    } else {
      for (const param of params) {
        if (!excluded.has(param.name))
          store.virtual[param.name] = store.makeVirtual('', param.name, param.type);
      }
    }
    return store;
  }

  // Events accept table arguments, so those values are in the store as tables.
  // When the table gets passed as a goal argument, it's converted to json, so in
  // the rule/task body, those variables will be a json string.  This method takes
  // the store created for the event and returns a new store with arrays properly
  // typed as a json string.
  toGoalStore() {
    var copy = this.clone();
    copy.squashTables(copy.virtual);
    return copy;
  }

  private squashTables(mapping: VirtualObj) {
    for (const name in mapping) {
      const local = mapping[name];
      if (typeof local === 'string') {
        const type = this.types[local];
        if (isConformingArray(type))
          this.types[local] = JsonValue;
      } else {
        this.squashTables(local);
      }
    }
  }

  makeVirtual(prefix: string, name: string, type: ValueType): VirtualMap {
    if (Array.isArray(type)) {
      const local = '$' + strJoin(prefix, name, '_');
      this.types[local] = type;
      return local;
    } else {
      switch (type.name) {
        case 'Int':
        case 'Number':
        case 'String':
        case 'Boolean':
          const local = '$' + strJoin(prefix, name, '_');
          this.types[local] = type;
          return local;
  
        default:
          const cls = getMetaStorage().getRequiredClass(type.name);
          var obj = {};
          for (const field in cls.fields) {
            obj[field] = this.makeVirtual(strJoin(prefix, name, '_'), field, cls.fields[field].type);
          }
          return obj;
      }  
    }
  }

  generateParameterExpansion(lines: string[], indent: string='  ') {
    for (const name in this.virtual) {
      const local = this.virtual[name];
      if (typeof local !== 'string') {
        const fields = this.generateDestructure(local);
        if (fields.length > 1) {
          const sep = `,\n${indent}  `;
          lines.push(`${indent}let {\n${indent}  ${fields.join(sep)}\n${indent}} = JsonParse($${name});`);
        } else
          lines.push(`${indent}let { ${fields[0]} } = JsonParse($${name});`);
      }
    }
  }

  generateAssign(rhs: string, lines: string[], indent: string='  ', prefix: string='', term: string=';') {
    if (this.scalar) {
      const type = this.types[this.scalar];
      if (isConformingArray(type)) {
        const cols = arrayColumns(type);
        lines.push(`${indent}${prefix}let ${this.scalar}(${cols.join(', ')}) = ${rhs}${term}`);
      } else {
        lines.push(`${indent}${prefix}let ${this.scalar} = ${rhs}${term}`);
      }
    } else {
      const fields = this.generateDestructure(this.virtual);
      if (fields.length > 1) {
        const sep = `,\n${indent}  `;
        lines.push(`${indent}${prefix}let {\n${indent}  ${fields.join(sep)}\n${indent}} = ${rhs}${term}`);
      } else
        lines.push(`${indent}${prefix}let { ${fields[0]} } = ${rhs}${term}`);
    }
  }

  generateDestructure(mapping: VirtualObj) {
    const fields = Object.keys(mapping).map(name => {
      const local = mapping[name];
      if (typeof local === 'string') {
        const type = this.types[local];
        if (isConformingArray(type)) {
          const cols = arrayColumns(type);
          return `${name}: ${mapping[name]}(${cols.join(', ')})`;
        } else {
          return `${name}: ${mapping[name]}`;
        }
      } else {
        const subfields = this.generateDestructure(local);
        return `${name}: { ${subfields} }`;
      }
    });
    return fields;
  }

  generateRpcArgument(name: string, local: string) {
    const fieldType = this.types[local];
    if (toParaflowType(fieldType) === 'json')
      return `${name} -> JsonParse(${local})`;
    else if (local.slice(1) !== name)
      return `${name} -> ${local}`;
    return local;
  }

  generateRpcField(name: string, local: string, shortHand: boolean = true) {
    const fieldType = this.types[local];
    if (toParaflowType(fieldType) === 'json')
      return `${name}: JsonParse(${local})`;
    else if (!shortHand || local.slice(1) !== name)
      return `${name}: ${local}`;
    return local;
  }
}

// Lhs is a mapping from the return value object structure
// to local variables.
type VirtualMap = string | VirtualObj;
type VirtualObj = Record<string,any>;

function makeCallParamMap(params: MetaParameter[], store: VirtualStore) {
  var args = [];
  for (const name in store.virtual) {
    const desc = store.virtual[name];
    if (typeof desc === 'string') {
      args.push(store.generateRpcArgument(name, desc));
    } else {
      args.push(`${name} -> { ${makeCallObject(desc, store)} }`);
    }
  }
  return args.join(', ');
}

function makeCallObject(obj: VirtualObj, store: VirtualStore) {
  var params = [];
  for (const name in obj) {
    if (typeof obj[name] === 'string') {
      params.push(store.generateRpcField(name, obj[name], false));
    } else {
      params.push(`${name}: { ${makeCallObject(obj[name], store)} }`);
    }
  }
  return params.join(', ');
}

const isNotHidden = (field: MetaField) => !field.options.hidden;

function makeResponse(type: ValueType, store: VirtualStore) {
  var args = [];
  if (store.scalar) {
    args.push(store.generateRpcArgument(store.scalar.slice(1), store.scalar))
  } else if (type instanceof Function) {
    const cls = getMetaStorage().getRequiredClass(type.name);
    for (const name in cls.fields) {
      if (isNotHidden(cls.fields[name])) {
        const fieldType = cls.fields[name].type;
        const desc = store.virtual[name];
        if (typeof desc === 'string') {
          args.push(store.generateRpcArgument(name, desc))
        } else if (!Array.isArray(fieldType)) {
          const cls2 = getMetaStorage().getRequiredClass(fieldType.name);
          const prj = makeResponseObject(cls2, desc, store, isNotHidden);
          args.push(`${name} -> { ${prj} }`);
        } else
          throw new Error('Internal error: makeResponse array should be local variable');
      }
    }
  }
  return args.join(', ');
}

function makeResponseObject(cls: ClassDefinition, obj: VirtualObj, store: VirtualStore, filter: (field: MetaField) => boolean) {
  var params = [];
  for (const name in cls.fields) {
    if (filter(cls.fields[name])) {
      const fieldType = cls.fields[name].type;
      if (typeof obj[name] === 'string') {
        params.push(store.generateRpcField(name, obj[name]));
      } else if (!Array.isArray(fieldType)) {
        const cls2 = getMetaStorage().getRequiredClass(fieldType.name);
        const prj = makeResponseObject(cls2, obj[name], store, filter);
        params.push(`${name}: { ${prj} }`);
      } else
        throw new Error('Internal error: makeResponseObject array should be local variable');
    }
  }
  return params.join(', ');
}

const isExported = (field: MetaField) => field.options.save || (typeof field.options.key === 'boolean' && field.options.key);

function makeReturn(name: string, type: ValueType, store: VirtualStore) {
  if (store.scalar) {
    return store.scalar;
  } else if (!Array.isArray(type)) {
    const cls = getMetaStorage().getRequiredClass(type.name);
    const prj = makeReturnObject(cls, store.virtual, isExported);
    if (Object.keys(prj).length > 0)
      return `{ ${prj} }`;
  }
}

function makeReturnObject(cls: ClassDefinition, obj: VirtualObj, filter: (field: MetaField) => boolean) {
  var params = [];
  for (const name in cls.fields) {
    if (filter(cls.fields[name])) {
      if (typeof obj[name] === 'string') {
        params.push(`${name}: ${obj[name]}`);
      } else {
        const fieldType = cls.fields[name].type;
        if (Array.isArray(fieldType)) {
        } else {
          const cls2 = getMetaStorage().getRequiredClass(fieldType.name);
          const prj = makeReturnObject(cls2, obj[name], filter);
          params.push(`${name}: { ${prj} }`);
        }
      }
    }
  }
  return params.join(', ');
}

function reportInputs(params: MetaParameter[], inputKey?: MetaField) {
  console.log('Inputs:');
  if (isObjectParameter(params)) {
    const type = params[0].type;
    const cls = getMetaStorage().getRequiredClass(type.name);
    Object.keys(cls.fields)
      .filter(field => ! cls.fields[field].options.restore && cls.fields[field].options.key !== 'auto')
      .forEach(field => {
        var type = toParaflowType(cls.fields[field].type);
        if (inputKey && field === inputKey.name)
          type += ' (key)';
        console.log(`\t${field}: ${type}`);
      });
  } else {
    params.forEach(param => {
      var type = toParaflowType(param.type);
      if (inputKey && param.name === inputKey.name)
        type += ' (key)';
      console.log(`\t${param.name}: ${type}`);
    });
  }
}

function reportOutputs(retType: ValueType, outputKey?: MetaField) {
  console.log('Outputs:')
  if (Array.isArray(retType)) {
    console.log(`\t${retType[0].name}[]`)
  } else if (isScalarType(retType)) {
    console.log(`\t${retType.name}`)
  } else {
    const cls = getMetaStorage().getRequiredClass(retType.name);
    for (const name in cls.fields) {
      const field = cls.fields[name];
      if (isNotHidden(field)) {
        var type = toParaflowType(field.type);
        if (outputKey && name === outputKey.name)
          type += ' (key)';
        console.log(`\t${name}: ${type}`);  
      }
    }      
  }
}

function isConformingArray(type: ValueType): type is Function[] {
  return Array.isArray(type) && isConformingElement(type[0]);
}

function nonConformingArray(type: ValueType): type is Function[] {
  return Array.isArray(type) && !isConformingElement(type[0]);
}

function isConformingElement(type: ValueType) {
  if (!Array.isArray(type) && !isScalarType(type)) {
    const cls = getMetaStorage().getRequiredClass(type.name);
    for (const name in cls.fields) {
      if (!isScalarType(cls.fields[name].type))
        return false;
    }
    return true;
  }
  return false;
}

function arrayColumns(type: Function[]) {
  const cls = getMetaStorage().getClass(type[0].name);
  if (cls)
    return Object.keys(cls.fields);
  throw new Error(`Internal error arrayColumns`)
}

function toParaflowType(jsType: ValueType) {
  var pfType = 'json';
  if (Array.isArray(jsType)) {
    if (isConformingArray(jsType)) {
      const cls = getMetaStorage().getClass(jsType[0].name);
      const cols = Object.keys(cls.fields).map(name => `${name} ${toParaflowType(cls.fields[name].type)}`);
      pfType = `table(${cols.join(', ')})`;
    }
  } else {
    switch (jsType.name) {
      case 'Int':
        pfType = 'int';
        break;
      case 'Number':
        pfType = 'double';
        break;
      case 'String':
        pfType = 'string';
        break;
      case 'Boolean':
        pfType = 'int';
        break;
    }  
  }
  return pfType;
}

