const slashRe = /[/\\]/g;
const invalidCharRe = /[^0-9A-Za-z_]/g;

function makeCamelCaseName(name: string) {
  const parts = name.split(slashRe);
  const cleaned = parts.map(s => {
    const clean = s.replaceAll(invalidCharRe, '_')
    const snake = clean.split('_').map(w => capitalize(w)).join('');
    return snake;
  });
  return cleaned.join('_');
}

const camelCaseToDisplayRe = /([a-zA-Z])(?=[A-Z])/g;
function splitCamelCaseToDisplay(cc: string) {
  return cc.replace(camelCaseToDisplayRe, '$1 ').split(' ');
};

function capitalize(s: string) {
  return s[0].toUpperCase() + s.slice(1);
}

// Join two strings with separator if both are non-empty,
// otherwise just return the non-empty one.

function strJoin(s1: string, s2: string, sep: string = ', ') {
  if (s1 && s2)
    return s1 + sep + s2;
  else if (s1)
    return s1;
  else
    return s2;
}